import PropTypes from 'prop-types'
import { cx, css } from 'emotion'

const MainFL = ({ backgroundColor, backgroundImage, width, margin, display, defaultHeight,  style, paddingRoot, ...props }) => {
  const baseStyle = css`
      ${ width && `width: ${typeof width === 'string' ? width : width+'%' }`};
      ${ backgroundColor && `background-color: ${ backgroundColor };` };
      margin: ${ typeof margin === 'string' ? margin : margin+'%' };
      display: ${ display };
      ${ style };
    `
  const defautlStyle = css`
    width: 100%;
    float: left;
    ${ backgroundColor && `background-color: ${ backgroundColor }`};
    ${ defaultHeight && `height: 0` };
    ${ backgroundImage && `
      background: linear-gradient(rgba(0, 0, 0, 0.7),rgba(0, 0, 0, 0.7)),url("${ backgroundImage }");
      background-size: cover;
      background-repeat: no-repeat;
    `};
    ${ paddingRoot && `padding: 50px 0px` };
  `
  return (
    <div className={ defautlStyle }>
      <div className={ cx(baseStyle, style) }>
        { props.children }
      </div>
    </div>
  )
}

MainFL.propTypes = {
  backgroundColor: PropTypes.string,
  width:PropTypes.string || PropTypes.number,
  margin:PropTypes.string || PropTypes.number,
  display:PropTypes.string,
}

MainFL.defaultProps = {
  backgroundColor: 'transparent',
  width: '100%',
  margin: '20px auto',
  display: 'block',
}

export default MainFL
